export default {
	data() {
		return {
			isdisabled:false
		};
	},
	props: {
		groupLogo: {
			type: String,
			default: '../../static/logo.jpg'
		},
		groupName:{
			type:String,
			required:true
		},
		activityStartTime:{
			type:String,
			required:true
		},
		imgs:{
			type:Array
		},
		text:{
			type:String
		},
		activityId:{
			type:String,
			required:true
		},
		userId:{
			type:Number,
		}
	},
	methods:{

		toUserDetail(){
			uni.navigateTo({
				url:"/pages/groupDetail/groupDetail",
				animationDuration:500,
				animationType:'auto'
			})
		},
		followerUser(){
			this.isdisabled = true
			uni.showToast({
				title:"已关注该用户"
			})
		}
	}
}
